body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    margin: 0;
    padding: 0;
}
.container-default {
    height: 100vh;
    width:100%;
}

.App .header {
    text-align: center;
    color:white;
    background-image: -webkit-gradient(linear, left top, right top, from(rgb(30,99,139)) , to(rgb(33,137,105)));
    background-image: -webkit-linear-gradient(left, rgb(30,99,139) , rgb(33,137,105));
    background-image: linear-gradient(to right, rgb(30,99,139) , rgb(33,137,105));
    padding-top: 40px;
    padding-bottom: 40px;
}

.App .header h1 {
    font-size: 2.5em;
}
.loading {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
    margin: 0 auto;
    margin-top: 250px;
  }
  
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
/* clear floats */
.clearfix {
    clear: both;
}
.searchBar {
    text-align: center;
    margin-top: 20px;
}
 h2 {
    font-weight: 400;
}
.searchContent {
    width:100%;
}

.searchBar input {
    width: 90%;
    height: 56px;
    padding: 10px;
    border: 1px solid rgb(170,170,170);
    font-size: 1.5em;
}
#searchIcon {
    float: right;
    margin-right: 20px;
}
#searchIcon:hover {
    cursor: pointer;
}
img:hover {
    cursor: pointer;
}
.iconstar {
    float: left;
    margin-right:10px;
}
.description  {
    font-size: 1.5em;
    line-height: 40px;
    padding: 10px;
    color:#212121;
}
.description div ul li span {
    font-size: 1em !important;
    padding: 10px;
    color:#212121;
}
.card {
    width: 95%;
    padding-right:50px;
    margin: 0 auto;
    margin-top: 20px;
    text-align:left;
}

